.payment{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    color: black;
    margin:30px;
    padding: 15px;
    border-radius: 10px;
}

.payment input{
    width: 50%;
    height: 50px;
    border-radius: 10px;
    border-style: solid;
    padding: 10px;
    margin: 10px;
    color:white;
}

.payment button{
    width: 200px;
    height: 50px;
    border-width: 0px;
    background: black;
    color:wheat;
    margin: 10px;
}